export default {
	data() {
		return {
			isLogin: false,




			isH5Plus: false,

			userinfo: {},
			orderTypeLise: [
				//name-标题 icon-图标 badge-角标
				{ name: '全部网站', icon: 'l1.png' },
				{ name: '已开通网站', icon: 'l2.png' },
				{ name: '未开通网站', icon: 'l3.png' }
				// { name: '待评价', icon: 'l4.png', badge: 9 },
				// { name: '退换货', icon: 'l5.png', badge: 0 }
			],
			severList: [
				[
					{ name: '发布结果', icon: 'security.png' },
					{ name: '我的收藏', icon: 'quan.png' },
					{ name: '开通会员', icon: 'mingxi.png' },
					{ name: '向平台推荐网站', icon: 'addr.png' },
					{ name: '历史推荐网站', icon: 'bank.png' },
					{ name: '联系我们', icon: 'kefu.png' },
					// { name: '向平台推荐网站', icon: 'momey.png' },
				]
				// [
				// 	// { name: '抽奖', icon: 'choujiang.png' },
				// 	// { name: '向平台推荐网站', icon: 'addr.png' },
				// 	// { name: '历史推荐网站', icon: 'bank.png' },
				// 	// { name: '联系我们', icon: 'kefu.png' },
				// 	// { name: '发布结果', icon: 'point.png' },
				// 	// { name: '任务', icon: 'renw.png' }
				// ]
			]
		};
	},
	onLoad() {
		//加载
		this.init();
	},
	methods: {
		init() {
			//用户信息
			this.userinfo = {
				face: '../../static/HM-PersonalCenter/face.jpg',
				username: '开单王08042',
				integral: '钻石会员'
			};
		},
		//用户点击订单类型
		toOrderType(index) {
			let name =this.orderTypeLise[index].name
			if(name == '全部网站'){
				uni.navigateTo({
					url: '/pages/center/layout/zhandian/zhandian'
				});
			}else if(name == '已开通网站'){
				uni.navigateTo({
					url: '/pages/center/layout/allinter/allinter'
				});
			}else if(name == '未开通网站'){
				uni.navigateTo({
					url: '/pages/center/layout/closeinter/closeinter'
				});
			}
			// uni.showToast({ title: this.orderTypeLise[index].name });
		},
		//用户点击列表项
		toPage(list_i, li_i) {
			uni.showToast({ title: this.severList[list_i][li_i].name });
		}
	}
};
